.calendar_container {
  width: 300px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  margin: 0 auto;
  text-align: center;
}
.calendar_container .headingtext {
  margin-bottom: 20px;
}
.calendar_container .headingtext .headingtext h1 {
  font-size: 30px;
}
.calendar_container .type {
  display: flex;
  justify-content: space-evenly;
  width: 100%;
  margin-bottom: 20px;
}
.calendar_container .type button {
  width: 50px;
  height: 15px;
  border: none;
  border-radius: 15px;
}
.calendar_container .type .leftred {
  display: flex;
  font-size: 14px;
}
.calendar_container .type .leftred .red {
  display: flex;
  justify-content: flex-start;
  background: #DC2F34;
}
.calendar_container .type .rightgreen {
  display: flex;
  font-size: 14px;
}
.calendar_container .type .rightgreen .green {
  display: flex;
  justify-content: flex-end;
  background: #00904A;
}
.calendar_container .calendar {
  width: 100%;
  height: auto;
  box-shadow: 0 0.5rem 3rem rgba(0, 0, 0, 0.4);
}

.create {
  font-size: 16px;
}

.create a {
  text-decoration: none;
  color: #000;
}

.month {
  height: 80px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 0 2rem;
  text-align: center;
  text-shadow: 0 0.3rem 0.5rem rgba(0, 0, 0, 0.5);
  background: lightgray;
}

.prev-date,
.next-date {
  opacity: 0.2;
}

.weekdays {
  width: 100%;
  height: 50px;
  padding: 0 5px;
  display: flex;
  justify-content: space-evenly;
  align-items: center;
}

.weekdays div {
  font-size: 14px;
  font-weight: 400;
  letter-spacing: 0.1rem;
  width: 5.7142857143rem;
  display: flex;
  justify-content: center;
  align-items: center;
  text-shadow: 0 0.3rem 0.5rem rgba(0, 0, 0, 0.5);
  font-weight: bold;
}

.days {
  width: 100%;
  display: flex;
  flex-wrap: wrap;
  padding: 0 10px;
  font-size: 14px;
}

.days div {
  width: 40px;
  margin: 0px;
  height: 40px;
  display: flex;
  justify-content: center;
  align-items: center;
  transition: background-color 0.1s;
}

.days div:hover:not(.today) {
  background-color: #005140;
  border-radius: 30px;
  cursor: pointer;
}

.today {
  background-color: lightgray;
  border-radius: 30px;
}

.days .lists {
  background-color: #00904A;
  border-radius: 30px;
}

.days .listsred {
  background-color: #DC2F34;
  border-radius: 30px;
}

.red {
  background-color: #DC2F34;
  border-radius: 30px;
}

.month i {
  font-size: 20px;
  cursor: pointer;
  color: #000;
}

.month .date p {
  font-size: 14px;
  font-weight: lighter;
}

/*Responsive table Driver*/
