  margin-top: 20px;
  height: 100vh;
}

.table_container {
  width: 100%;
  margin: 0 auto;
  height: auto;
  height: 17vh;
  margin-bottom: 100px;
  overflow-x: visible;
}

table {
  width: 100%;
  border-collapse: collapse;
  color: #f3f3f3;
}

td, th {
  padding: 8px;
  text-align: left;
}

/* Zebra striping */
.avl1 {
  background: #DC2F34;
  border-bottom: 1px solid #FFFFFF;
}

/* Zebra striping */
.avl0 {
  background: #00904A;
  border-bottom: 1px solid #FFFFFF;
}

thead {
  height: 50px;
}
thead th {
  background: #EAEAEA;
  font-weight: bold;
  color: #000000;
